import { createSlice } from '@reduxjs/toolkit';

import axios from "axios";

import {
    API_AGENTS_LIST_URL,
} from '../../../../api/env';

const initialState:any = {
    data:[],
};

const agentSlice = createSlice({
    name: 'agents',
    initialState: initialState,
    reducers: {
        SET_DATA: (state, action) => {
            return {
                ...state,
                data: [...action.payload.data],
            };
        },
    }
});

